<template>
  <page-title/>

  <form @submit.prevent="onFormSubmit" class="panel">

    <div class="">
      <label class="form-label">پرسش</label>
      <input type="text" class="form-control" v-model="model.question" required/>
    </div>

    <div class="mt-2">
      <label class="form-label">پاسخ</label>
      <textarea class="form-control" v-model="model.answer"></textarea>
    </div>

    <ul class="mt-2 text-danger">
      <li v-for="(x,k) in errors" :key="k">{{ x }}</li>
    </ul>

    <div class="mt-3 d-flex flex-row-reverse">
      <button class="btn btn-primary" type="submit">
        <span class="fa fa-check"></span>
        <span class="ms-1">ثبت</span>
      </button>

      <router-link to="/qna" class="btn btn-warning me-2">
        <span class="fa fa-times"></span>
        <span class="ms-1">انصراف</span>
      </router-link>
    </div>
  </form>
</template>

<script>
import PageTitle from '../../tools/PageTitle'
import { dispatchNotification } from '../../library/stores/notifications'

export default {
  name: 'QuestionForm',
  props: {
    qnAId: { default: undefined }
  },
  components: { PageTitle },
  data: () => ({
    workign: false,
    model: {
      question: '',
      answer: ''
    },
    errors: {}
  }),
  methods: {
    onFormSubmit() {
      if (this.working) {
        return
      }
      this.working = true
      this.$fetch('/qna', this.qnAId ? 'patch' : 'post', this.model)
          .then(() => {
            dispatchNotification(this.qnAId ? 'پرسش و پاسخ با موفقیت بروز شد' : 'پرسش و پاسخ با موفقیت ثبت شد')
            this.$router.push('/qna')
          })
          .finally(() => this.working = false)
    },
    retrieveModelInfo() {
      this.working = true
      this.$fetch(`/qna/${this.qnAId}`)
          .then(x => this.model = x.data)
          .finally(() => this.working = false)
    }
  },
  mounted() {
    if (this.qnAId) {
      this.retrieveModelInfo()
    }
  }
}
</script>

